import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

const Details = () => import('@/pages/details/details.vue');
const Description = () => import('@/pages/description/description.vue');
const Pay = () => import('@/pages/pay/pay.vue');
Vue.use(Router)

export default new Router({
  mode:'hash',
  routes: [
    {
      path: '/',
      name: 'Details',
      meta: {title: '商品详情'},
      component: Details
    },
    {
      path: '/description',
      name: 'Description',
      meta: {title: '商品参数'},
      component: Description
    },
    {
      path: '/pay',
      name: 'Pay',
      meta: {title: '支付成功'},
      component: Pay
    },
  ],
	
})



